@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
.container{display:flex;flex-direction:column;height:100vh;padding:7.5rem 7.5rem 7.5rem 10rem;transition:background-color 0.3s}@media (max-width: 768px){.container{padding:3rem}}.container_good-weather{transition:background-color 0.3s;background-color:#498CEC}.container_bad-weather{transition:background-color 0.3s;background-color:#51657E}

.header{display:flex;justify-content:space-between}@media (max-width: 768px){.header{align-items:center;flex-direction:column}}

.main{flex-grow:1}

.footer{display:flex;justify-content:space-between}@media (max-width: 768px){.footer{flex-direction:column}}

.button{display:flex;align-items:center;padding:0;transition:opacity 0.3s;opacity:0.6;color:inherit;border:none;background:none;font-size:1.8rem;line-height:2.2rem}.button:hover{transition:opacity 0.3s;opacity:1}.button svg{margin-right:1.2rem}.button_outlined{padding:5px;color:#fff;border:1px solid #1086FF;border-radius:5px;background:#1086FF}

.input__container{display:flex;padding:2.7rem;border-radius:8px;background:#fff}@media (max-width: 768px){.input__container{width:100%}}.input__field{flex-grow:1;border:none;background:none;font-size:3rem;line-height:3.6rem}.input__confirm-button{transition:color 0.3s;color:#1086FF;border:none;background:none;font-size:3rem;line-height:3.6rem}.input__confirm-button:disabled{transition:color 0.3s;color:#CCC}

.city-name__container{display:flex;align-items:center;margin-top:1rem}.city-name__container button{margin-right:3rem}.city-name__container button:last-child{margin-right:0}@media (max-width: 768px){.city-name__title{text-align:center}}

.switch__container{display:inline-flex;border:1px solid rgba(255,255,255,0.6);border-radius:8px;font-size:1.8rem}.switch__item input[type=radio]{display:none}.switch__item:first-child label{border-top-left-radius:8px;border-bottom-left-radius:8px}.switch__item:last-child label{border-top-right-radius:8px;border-bottom-right-radius:8px}.switch__item label{display:inline-block;padding:.5rem 1.5rem;cursor:pointer;-webkit-user-select:none;-ms-user-select:none;user-select:none;transition:background-color, opacity 0.3s;opacity:0.6}.switch__item input[type=radio]:checked+label{transition:background-color, opacity 0.3s;opacity:1;background:#6DA3F0}

@media (max-width: 768px){.weather-unit-toggler__container{margin-top:2rem}}.weather-unit-toggler__symbol{font-size:1.8rem;margin-right:1rem;opacity:0.6}

.weather-info__container{display:flex;align-items:center;justify-content:center;flex-direction:column;width:100%;height:100%}.weather-info__type{font-size:2.5rem;line-height:3rem}.weather-info__value{display:flex;align-items:center}.weather-info__icon{margin:3.2rem}@media (max-width: 768px){.weather-info__icon{width:30%}}.weather-info__error{font-size:3rem;color:red;margin-top:1rem}

.value-display__container{display:flex;flex-direction:column}.value-display__value{font-size:2.5rem;line-height:3rem}

/* http://meyerweb.com/eric/tools/css/reset/
   v2.0-modified | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;

    vertical-align: baseline;

    border: 0;

    font: inherit;
    font-size: 100%;
}

/* make sure to set some focus styles for accessibility */
:focus {
    outline: 0;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}

body {
    line-height: 1;
}

ol,
ul {
    list-style: none;
}

blockquote,
q {
    quotes: none;
}

blockquote::before,
blockquote::after,
q::before,
q::after {
    content: '';
    content: none;
}

table {
    border-spacing: 0;

    border-collapse: collapse;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
    -webkit-appearance: none;
    -moz-appearance: none;
}

input[type=search] {
    -webkit-appearance: none;
    -moz-appearance: none;
    box-sizing: content-box;
}

textarea {
    overflow: auto;

    resize: vertical;
    vertical-align: top;
}

/**
 * Correct `inline-block` display not defined in IE 6/7/8/9 and Firefox 3.
 */

audio,
canvas,
video {
    display: inline-block;
    *display: inline;
    *zoom: 1;

    max-width: 100%;
}

/**
 * Prevent modern browsers from displaying `audio` without controls.
 * Remove excess height in iOS 5 devices.
 */

audio:not([controls]) {
    display: none;

    height: 0;
}

/**
 * Address styling not present in IE 7/8/9, Firefox 3, and Safari 4.
 * Known issue: no IE 6 support.
 */

[hidden] {
    display: none;
}

/**
 * 1. Correct text resizing oddly in IE 6/7 when body `font-size` is set using
 *    `em` units.
 * 2. Prevent iOS text size adjust after orientation change, without disabling
 *    user zoom.
 */

html {
    font-size: 100%; /* 1 */
    -webkit-text-size-adjust: 100%; /* 2 */
    -ms-text-size-adjust: 100%; /* 2 */
}

/**
 * Address `outline` inconsistency between Chrome and other browsers.
 */

a:focus {
    outline: thin dotted;
}

/**
 * Improve readability when focused and also mouse hovered in all browsers.
 */

a:active,
a:hover {
    outline: 0;
}

/**
 * 1. Remove border when inside `a` element in IE 6/7/8/9 and Firefox 3.
 * 2. Improve image quality when scaled in IE 7.
 */

img {
    border: 0; /* 1 */
    -ms-interpolation-mode: bicubic; /* 2 */
}

/**
 * Address margin not present in IE 6/7/8/9, Safari 5, and Opera 11.
 */

figure {
    margin: 0;
}

/**
 * Correct margin displayed oddly in IE 6/7.
 */

form {
    margin: 0;
}

/**
 * Define consistent border, margin, and padding.
 */

fieldset {
    margin: 0 2px;
    padding: 0.35em 0.625em 0.75em;

    border: 1px solid #C0C0C0;
}

/**
 * 1. Correct color not being inherited in IE 6/7/8/9.
 * 2. Correct text not wrapping in Firefox 3.
 * 3. Correct alignment displayed oddly in IE 6/7.
 */

legend {
    *margin-left: -7px; /* 3 */
    padding: 0;

    white-space: normal; /* 2 */

    border: 0; /* 1 */
}

/**
 * 1. Correct font size not being inherited in all browsers.
 * 2. Address margins set differently in IE 6/7, Firefox 3+, Safari 5,
 *    and Chrome.
 * 3. Improve appearance and consistency in all browsers.
 */

button,
input,
select,
textarea {
    margin: 0; /* 2 */

    vertical-align: baseline; /* 3 */
    *vertical-align: middle; /* 3 */

    font-size: 100%; /* 1 */
}

/**
 * Address Firefox 3+ setting `line-height` on `input` using `!important` in
 * the UA stylesheet.
 */

button,
input {
    line-height: normal;
}

/**
 * Address inconsistent `text-transform` inheritance for `button` and `select`.
 * All other form control elements do not inherit `text-transform` values.
 * Correct `button` style inheritance in Chrome, Safari 5+, and IE 6+.
 * Correct `select` style inheritance in Firefox 4+ and Opera.
 */

button,
select {
    text-transform: none;
}

/**
 * 1. Avoid the WebKit bug in Android 4.0.* where (2) destroys native `audio`
 *    and `video` controls.
 * 2. Correct inability to style clickable `input` types in iOS.
 * 3. Improve usability and consistency of cursor style between image-type
 *    `input` and others.
 * 4. Remove inner spacing in IE 7 without affecting normal text inputs.
 *    Known issue: inner spacing remains in IE 6.
 */

button,
html input[type="button"], /* 1 */
input[type="reset"],
input[type="submit"] {
    *overflow: visible; /* 4 */

    cursor: pointer; /* 3 */
    -webkit-appearance: button; /* 2 */
}

/**
 * Re-set default cursor for disabled elements.
 */

button[disabled],
html input[disabled] {
    cursor: default;
}

/**
 * 1. Address box sizing set to content-box in IE 8/9.
 * 2. Remove excess padding in IE 8/9.
 * 3. Remove excess padding in IE 7.
 *    Known issue: excess padding remains in IE 6.
 */

input[type="checkbox"],
input[type="radio"] {
    box-sizing: border-box; /* 1 */
    *width: 13px; /* 3 */
    *height: 13px; /* 3 */
    padding: 0; /* 2 */
}

/**
 * 1. Address `appearance` set to `searchfield` in Safari 5 and Chrome.
 * 2. Address `box-sizing` set to `border-box` in Safari 5 and Chrome
 *    (include `-moz` to future-proof).
 */

input[type="search"] {
    -webkit-appearance: textfield; /* 1 */ /* 2 */
    box-sizing: content-box;
}

/**
 * Remove inner padding and search cancel button in Safari 5 and Chrome
 * on OS X.
 */

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
}

/**
 * Remove inner padding and border in Firefox 3+.
 */

button::-moz-focus-inner,
input::-moz-focus-inner {
    padding: 0;

    border: 0;
}

/**
 * 1. Remove default vertical scrollbar in IE 6/7/8/9.
 * 2. Improve readability and alignment in all browsers.
 */

textarea {
    overflow: auto; /* 1 */

    vertical-align: top; /* 2 */
}

/**
 * Remove most spacing between table cells.
 */

table {
    border-spacing: 0;

    border-collapse: collapse;
}

html,
button,
input,
select,
textarea {
    color: #222;
}

::selection {
    background: #B3D4FC;
    text-shadow: none;
}

img {
    vertical-align: middle;
}

fieldset {
    margin: 0;
    padding: 0;

    border: 0;
}

textarea {
    resize: vertical;
}

.chromeframe {
    margin: 0.2em 0;
    padding: 0.2em 0;

    color: #000;
    background: #CCC;
}

body{font-family:'Lato', sans-serif}html,body{height:100%;color:#fff}html{box-sizing:border-box;font-size:10px}@media (max-width: 768px){html{font-size:6px}}*,*::before,*::after{box-sizing:inherit}h1{font-size:18rem;line-height:21.6rem}@media (max-width: 768px){h1{font-size:9rem;line-height:10rem}}h2{font-size:5rem;line-height:6rem}h3{opacity:0.6;font-size:1.8rem;line-height:2.2rem}svg{fill:currentColor}

